import React from 'react';
import './css/App.css';
import {Route, BrowserRouter} from 'react-router-dom';
import Home from './components/Home';
import Usage from './components/Usage/Usage';
import Session from './components/Session/Session';

import Intro from './components/Intro/Intro';
import Intro2 from './components/Intro/Intro2';
import Intro3 from './components/Intro/Intro3';
import Intro4 from './components/Intro/Intro4';
import Intro5 from './components/Intro/Intro5';
import Shop from './components/Shop/Shop';


const App = () => (
    <div className="App">
        
    <BrowserRouter basename="/prot1/">
        <div>
            <Route exact path={"/"} component={Intro}/>
            <Route path={"/1"} component={Intro2}/>
            <Route path={"/2"} component={Intro3}/>
            <Route path={"/3"} component={Intro4}/>
            <Route path={"/4"} component={Intro5}/>
            <Route path={"/start"} component={Home}/>
            <Route path={"/verbruik"} component={Usage}/>
            <Route path={"/laadsessie"} component={Session}/>
            <Route path={"/shop"} component={Shop}/>
        </div>
    </BrowserRouter>   
    </div>
);

export default App;



