import React from 'react';
import avatar from '../../img/bot.svg';
import Typing from 'react-typing-animation';
import {Link} from 'react-router-dom';
import './Bot.css';

const Bot3 = () => (
    <div className="bot-section">
        <div className="speech-cloud">
        <Typing speed={20}>
            <p>Bij elke laadsessie bouw je jouw financile beloning op, wat jij vervolgens kunt uitwisselen voor fikse kortingen in de app!</p>
            <Link to="/3" className="next-btn">
                Doorgaan
            </Link>
        </Typing>
    </div>
        <img src={avatar} alt="bot"/>
    </div>
);

export default Bot3;


