import React from 'react';
import avatar from '../../img/bot.svg';
import Typing from 'react-typing-animation';
import {Link} from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Msg = ({closeToast}) => (
    <p>Begin nu met slim laden en profiteer van de voordelen!</p>    
);

const Bot5 = () => (
    <div className="bot-section">
        <div className="speech-cloud">
        <Typing speed={20}>
            <p>Bedankt voor je tijd en stel een laadsessie in om te beginnen met slim laden. Je krijgt een bonus voor het doorlopen van de introductie! <b className="bonus">+ 2,00</b></p>
            <Link to="/start" className="next-btn" onClick={() => toast(<Msg />)}>
                Voltooien
            </Link>
        </Typing>
    </div>
        <img src={avatar} alt="bot"/>
    </div>
);

export default Bot5;


