import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './Session.css';
import Saved from './Saved';
import { ToastContainer, toast, Flip } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

class Session extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            items: [], 
            text: '' 
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    render() {

        const Msg = ({closeToast}) => (
            <p><b>Let op:</b><br></br> U bespaart  0,52 meer als je iets later vertrekt!</p>    
        );

        return (
            <div className="home-section">
                <div className="session-section">

                    <h1>Laadsessie instellen</h1>
                    <form onSubmit={this.handleSubmit}>
                        <label htmlFor="new-session">
                        Hoelaat ga je vertrekken?
                        </label>
                        <input
                        id="new-session"
                        onChange={this.handleChange}
                        value={this.state.text}
                        placeholder="07:00"
                        type="time"
                        />
                        <button onClick={() => toast(<Msg />)}>
                        Activeren
                        </button>
                    </form>
                    <Link to='/start' className='back-btn'>
                        Terug
                    </Link>
                    <Saved items={this.state.items} />

                </div>
            </div>
        );
    }

    handleChange(e) {
        this.setState({ text: e.target.value });
      }
    
    handleSubmit(e) {

    e.preventDefault();
    if (!this.state.text.length) {
        return;
    }
    const newItem = {
        text: this.state.text,
        id: Date.now()
    };
    this.setState(state => ({
        items: state.items.concat(newItem),
        text: ''
    }));
    }
}

export default Session;


