import React from "react";
import './Shop.css';
import {Link} from 'react-router-dom';


const Shop = () => (
    <div className="home-section">
        <div className="shop-section">
            <h1>Shop</h1>
            <div className="item">
                <span>Binnenkort beschikbaar!</span>
            </div>
            <Link to='/start' className='back-btn'>
                Terug
            </Link>
        </div>
    </div>
);

export default Shop;


