import React from 'react';
import avatar from '../../img/bot.svg';
import Typing from 'react-typing-animation';
import {Link} from 'react-router-dom';
import './Bot.css';

const Bot = () => (
    <div className="bot-section">
        <div className="speech-cloud">
        <Typing speed={20}>
            <p><b>Hallo gebruiker!</b> <br></br>Ik ben Alpha en ik zal je kort uitleggen wat jij met de app kunt doen.</p>
            <Link to="/1" className="next-btn">
                Doorgaan
            </Link>
        </Typing>
    </div>
        <img src={avatar} alt="bot"/>
    </div>
);

export default Bot;


