import React from 'react';
import {Link} from 'react-router-dom';
import './Usage.css';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

var settings = {
    dots: false,
    infinite: false,
    speed: 600,
    slidesToShow: 1,
    slidesToScroll: 1
};

let items = [ 
    "07-10-2018","09-10-2018","11-10-2018"
];
var listItems;

const Usage = () => (
    <div className="home-section">
        <div className="usage-section">
        <h1>Verbruik</h1>
            <h3>Overzicht laadsessies</h3>
            <Slider {...settings}>
                {
                    listItems = items.map((item) =>
                        <div classname="usage-block">
                            <p><b>Datum:</b> {item}</p>
                            <p><b>Kosten:</b>  5,43</p>
                            <p><b>kWh:</b> 50</p>
                            <p><b>Start laadsessie:</b> 00:00 uur</p>
                            <p><b>Stop laadsessie:</b> 08:00 uur</p>
                            <p><b>Kosten laadsessie:</b>  6,75</p>
                            <p><b>Beloning slim laden:</b>  2,50</p>
                            <p><b>Totale kosten:</b>  4,25</p>
                        </div>
                    )
                }
                
            </Slider>
            <Link to="/start" className="usage-btn">Terug</Link>
            
        </div>
    </div>
);

export default Usage;


