import React from 'react';
import avatar from '../../img/bot.svg';
import Typing from 'react-typing-animation';
import {Link} from 'react-router-dom';
import './Bot.css';

const Bot2 = () => (
    <div className="bot-section">
        <div className="speech-cloud">
        <Typing speed={20}>
            <p>Het onderdeel slim laden is hier te zien, waarbij de auto opgeladen wordt met duurzame energie en jij daarvoor financieel wordt beloond!</p>
            <Link to="/2" className="next-btn">
                Doorgaan
            </Link>
        </Typing>
    </div>
        <img src={avatar} alt="bot"/>
    </div>
);

export default Bot2;


